<template>
	<div>
		<ul class="croplist" v-if="cropList.length > 0">
			<li class="cropitem" v-for="item in cropList" :key="item.id">
        <div class="pic-wrap">
          <img v-if="item.crop.imgUrl" :src="item.crop.imgUrl | aliimg(90,90,true)" alt="" />
        </div>
				<div class="cropdes">
					<p class="cropname">
            <span class="breed">{{item.crop.cropName |txtClip(item.cropBreed)}}</span>
            <span class="croplevel" v-if="item.plantNum !== null">{{item.plantNum}}{{item.crop.cropCategoryUnit}}</span>
          </p>
          <!-- <p class="output">
            {{item.crop.cropSubjectIdentifier | plantStandard}}标准：{{item.plantStandardId | levelFn}}
          </p> -->
					<!--<p class="output" v-if="item.plantStatus==2">
            预计产量：{{item.forecastTotalOutput ? item.forecastTotalOutput+'Kg':'无'}}
          </p>
					<p class="output" v-else-if="item.plantStatus==3">
            实际产量：{{item.actualTotalOutput ? item.actualTotalOutput+'Kg':'无'}}
          </p>-->
					<p class="time">
            <span>{{item.plantStartDate | znDateFormat('yyyy.MM.dd')}}{{item.plantMethod | plantMethodCn}}</span>
            <span style="margin-left: 50px;">{{pickStartDateProcess(item)}}</span>
          </p>
				</div>
			</li>
		</ul>
		<div class="nodata" v-if="cropList.length === 0">
			<img src="../../assets/images/wuzuowu.png" alt="无数据" />
			<p>暂无种养品种</p>
		</div>
		<div class="loading" v-if="false" v-loading="true" element-loading-text="加载中..."></div>
	</div>
</template>

<script>
import { znDateFormat, differenceDay } from '@/common/fn/index';
export default {
  data () {
    return {
      vm: this
    };
  },
  props: ['cropList', 'curDate'],
  methods: {
    classReturn (ruleId) {
      switch (ruleId) {
        case 1:
          return 'background:#02B8FF';
        case 2:
          return 'background:#0FEF34';
        case 3:
          return 'background:#A778F9';
        case 4:
          return 'background:#E6AA4A';
        default:
          break;
      }
    },
    pickStartDateProcess (cropInfo) {
      const endingMapping = {
        agri: '采收',
        forestry: '采收',
        animal: '出栏',
        fishery: '捕捞'
      };
      return cropInfo.pickMonthStr
        ? cropInfo.pickMonthStr.replace(/-/, '.') + endingMapping[cropInfo.crop.cropSubjectIdentifier]
        : `第${differenceDay({ difTimestamp: this.curDate, originTimestamp: cropInfo.plantStartDate })}天`;
    }
  },
  filters: {
    txtClip: function (crop, breed) {
      if (breed) {
        return crop + ' - ' + breed;
      } else {
        return crop;
      }
    },
    levelFn: function (ruleId) {
      let Zh = '';
      switch (ruleId) {
        case 1:
          Zh = '有机';
          break;
        case 2:
          Zh = '绿色';
          break;
        case 3:
          Zh = '无公害';
          break;
        case 4:
          Zh = '普通';
          break;
        default:
          Zh = '未知';
          break;
      }
      return Zh;
    },
    plantMethodCn (plantMethod) {
      let info = '';
      switch (plantMethod) {
        case 1:
          info = '移栽';
          break;
        case 2:
          info = '育苗';
          break;
        case 3:
          info = '播种';
          break;
        case 10:
        case 11:
        case 12:
          info = '养殖';
          break;
        case 20:
        case 21:
          info = '投苗';
          break;
        default:
          break;
      }
      return info;
    },
    plantStandard (cropSubjectIdentifier) {
      const endingMapping = {
        agri: '种植',
        forestry: '种植',
        animal: '养殖',
        fishery: '养殖'
      };
      return endingMapping[cropSubjectIdentifier];
    }
  }
};
</script>

<style scoped lang="less">
  .flex(@x:center,@y:center){
    display:flex;
    display:-webkit-flex;
    flex-flow:row nowrap;
    justify-content:@x;
    align-items:@y;
  }
  .fontS(@s:14px){
    font-size:@s;
  }
  .textAlign(@align:center){
    text-align:@align;
  }
  .croplist{
    .fontS();
    .cropitem{
      background:#061F36;
      padding:15px;
      color:#6DC1CB;
      margin-bottom:20px;
      .textAlign(left);
      .flex(flex-start, center);
      .time{
        span{
          &:nth-child(1){
            display: inline-block;
            width: 120px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            vertical-align: middle;
          }
        }
      }
      .pic-wrap{
        border-radius:50%;
        width:90px;
        height:90px;
        min-width: 90px;
        max-width: 90px;
        margin-right:30px;
        overflow: hidden;
        background: url("~@/assets/images/overview/crop-no-pic-bg.png")no-repeat center center;
        background-size: 46px 40px;
        background-color: #0C5872;
        img{
          border: 0 none;
          display: block;
          width: 100%;
          height: 100%;
        }
      }
      .cropname{
        color:#A1EFF8;
        line-height:30px;
        width: 360px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        .fontS(16px);
        .croplevel{
          background-color: #02B8FF;
          .fontS(14px);
          color:#fff;
          line-height:22px;
          padding:0 10px;
          border-radius:11px;
          margin-left:10px;
        }
      }
      .output{
        line-height:26px;
        margin-top: 5px;
      }
      .time{
        line-height:26px;
        margin-top: 5px;
      }
    }
    .gray.cropitem{
      .breed,.output,.time{
        color:rgba(161,239,248,0.41);
      }
      &>img{
        opacity:0.6;
      }
    }
  }
  .nodata{
    height:460px;
    .flex();
    flex-flow:column;
    p{
      color:#6DC1CB;
      .fontS(20px);
      line-height:42px;
      margin-bottom: 30px;
    }
  }

  .loading /deep/ .el-loading-mask{background-color:rgba(0,0,0,0);padding-top:200px;}
</style>



